@import url(https://fonts.googleapis.com/css2?family=Exo+2:wght@900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@300;400;500;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Exo+2:wght@300;500;600;700&display=swap);
body {
  margin: 0;
  padding: 0;
  opacity: 100%;
  background-image: url(/weather_app/static/media/elektra.822da572.jpg);

  background-size: cover;
  /* background-color: black; */
  /* backface-visibility: inherit; */
}

.App {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.App {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.container {
  width: 500px;
  height: 500px;
  background-color: white;
  border-radius: 15px;
  padding-top: 10px;
  margin: 100px auto;

  box-shadow: 0px 0px 80px -15px #8a2d7c;
}

.header {
  width: 30%;
  padding: 20px;
  margin-bottom: 10px;
  margin-left: 30px;
}

.geolocation {
  position: relative;
  margin: auto;
  text-align: right;
  flex-grow: 2;

  padding: 0px 0px 0px 0px;
  margin-bottom: 5px;
  margin-right: 50px;
  font-family: "Work Sans", sans-serif;
  font-weight: 500;
  font-size: 11pt;
  color: rgb(224, 134, 60);
  line-height: 1.1em;
}

.geolocation b {
  font-size: 12pt;
  color: #2a0f44;
}

.heading {
  display: flex;
  flex-direction: row;
  margin-bottom: 10px;
  /* align-content: flex-end; */
}

.header h1 {
  font-family: "Exo 2", sans-serif;
  font-weight: 900;
  font-size: 20pt;
}

.header h2 {
  position: absolute;
  font-family: "Work Sans", sans-serif;
  padding-top: 5px;
  font-weight: 500;
  font-size: 11pt;
}

h1,
h2 {
  animation: none 0s ease 0s 1 normal none running;
  -webkit-backface-visibility: visible;
          backface-visibility: visible;
  background: transparent none repeat 0 0 / auto auto padding-box border-box scroll;
  border: medium none currentColor;
  border-collapse: separate;
  border-image: none;
  border-radius: 0;
  border-spacing: 0;
  bottom: auto;
  box-shadow: none;
  box-sizing: content-box;
  caption-side: top;
  clear: none;
  clip: auto;
  color: #000;
  -webkit-columns: auto;
          columns: auto;
  -webkit-column-count: auto;
          column-count: auto;
  -webkit-column-fill: balance;
          column-fill: balance;
  grid-column-gap: normal;
  -webkit-column-gap: normal;
          column-gap: normal;
  -webkit-column-rule: medium none currentColor;
          column-rule: medium none currentColor;
  -webkit-column-span: 1;
          column-span: 1;
  -webkit-column-width: auto;
          column-width: auto;
  content: normal;
  counter-increment: none;
  counter-reset: none;
  cursor: auto;
  direction: ltr;
  display: inline;
  empty-cells: show;
  float: none;
  font-family: serif;
  font-size: medium;
  font-style: normal;
  font-feature-settings: normal;
  font-variant: normal;
  font-weight: normal;
  font-stretch: normal;
  line-height: normal;
  height: auto;
  -webkit-hyphens: none;
      -ms-hyphens: none;
          hyphens: none;
  left: auto;
  letter-spacing: normal;
  list-style: disc outside none;
  margin: 0;
  max-height: none;
  max-width: none;
  min-height: 0;
  min-width: 0;
  opacity: 1;
  orphans: 2;
  outline: medium none invert;
  overflow: visible;
  overflow-x: visible;
  overflow-y: visible;
  padding: 0;
  page-break-after: auto;
  page-break-before: auto;
  page-break-inside: auto;
  perspective: none;
  perspective-origin: 50% 50%;
  position: static;
  right: auto;
  -moz-tab-size: 8;
       tab-size: 8;
  table-layout: auto;
  text-align: left;
  text-align-last: auto;
  text-decoration: none;
  text-indent: 0;
  text-shadow: none;
  text-transform: none;
  top: auto;
  transform: none;
  transform-origin: 50% 50% 0;
  transform-style: flat;
  transition: none 0s ease 0s;
  unicode-bidi: normal;
  vertical-align: baseline;
  visibility: visible;
  white-space: normal;
  widows: 2;
  width: auto;
  word-spacing: normal;
  z-index: auto;
  all: initial;
}

.city-form {
  position: relative;
  background-color: #2a0f44;
  width: 80%;
  height: 350px;
  margin-top: 10px;
  margin: 0px 40px 0px 40px;
  /* margin: 40px; */
  padding: 10px;
  border-radius: 10px;
}

.search-place {
  /* width: auto; */
  /* height: auto; */
  /* display: flex; */
  /* flex-direction: row; */
  margin: 10px;
}

.input-place {
  background: rgba(255, 255, 255, 0.5);
  margin-bottom: 10px;
  margin-top: 3px;
  /* margin-left: 3px; */
  line-height: 1.5em;
  font-family: "Work Sans", sans-serif;
  font-size: 15px;
  border: none;
  border-radius: 3px;
  padding: 5px 15px 5px 15px;
  color: rgb(255, 255, 255);
  box-sizing: border-box;
  width: 70%;
}

.input-place::-webkit-input-placeholder {
  color: white;
}

.input-place:-ms-input-placeholder {
  color: white;
}

.input-place::placeholder {
  color: white;
}

.search-button {
  border: none;
  background: rgb(224, 134, 60);
  line-height: 1.5em;
  margin-left: 5px;
  padding: 5px 15px 5px 15px;
  border-radius: 3px;
  font-family: "Work Sans", sans-serif;
  font-size: 15px;
  color: rgb(255, 255, 255);
  cursor: pointer;
  width: 28%;
  /* -webkit-transition:all .2s ease;
  transition:all .2s ease; */
}

.weather-screen {
  margin-top: 5px;
  text-align: center;
}

.weather-screen h2 {
  font-family: "Exo 2", sans-serif;
  font-weight: 700;
  font-size: 25pt;
  color: rgb(255, 255, 255);
  margin-bottom: 30px;
}

.weather-screen > p {
  margin: 5px;
  margin-left: 10px;
  font-family: "Work Sans", sans-serif;
  font-weight: 500;
  font-size: 15px;
  color: rgb(202, 202, 202);
}

.weather-screen .temperature {
  font-family: "Exo 2", sans-serif;
  font-size: 80px;
  font-weight: 700;
  text-align: center;
  color: rgb(255, 165, 91);
  margin-top: 5px;
  margin-bottom: 15px;
}

.weather-set b {
  font-weight: 400;
  color: rgb(255, 165, 91);
}

.weather-set {
  margin: auto;
  width: 100%;
  display: flex;
  /* position: absolute; */
  font-family: "Work Sans", sans-serif;
  font-size: 15px;
  color: rgb(211, 211, 211);
  /* padding: 5px; */
  line-height: 0.5em;
}

.weather-set p {
  margin: 15px 25px 15px 35px;
  text-align: left;
  /* margin-top: 5px;
  margin-left: 25px; */
  font-weight: 300;
}

.welcome {
  margin: auto;
  width: 80%;
  text-align: center;
  margin-top: 20%;
  font-family: "Work Sans", sans-serif;
  font-size: 15px;
  color: rgb(211, 211, 211);
}

.welcome p {
  margin: 5px;
}

.no-result h1,
.welcome h1 {
  font-family: "Work Sans", sans-serif;
  font-weight: 600;
  font-size: 35px;
  /* color: rgb(211, 211, 211); */
  color: rgb(255, 165, 91);
}

.no-result {
  margin-top: 20%;
  width: 100%;
  text-align: center;
  font-family: "Work Sans", sans-serif;
  font-weight: 400;
  font-size: 15px;
  color: white;
}

